import { types } from "../types.js/types";


export const reducerHeroes = (state={},action) => {
    
    switch (action.type) {
        case types.login:
            return{
            ...action.payload,
            logged:true

        }
        case types.logout:
            return{
               logged:false

        }
            
           
        default:return state
            
    }
}
